// cancel bill
export function cancelBill(id) {
    return new Promise((res, rej) => {
        var config = {
            method: 'put',
            url: url + '/api/v3/user/bill/cancel/' + id,
            headers: {
                'Content-Type': 'application/json'
            },
        };
        axios(config)
            .then(function (response) {

                res(response.data);
            })
            .catch(function (error) {
                console.log(error);
                rej(error);
            });

    })
}


